@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 1) {
    @content;
  }
}

@mixin viewport--lg {
  @media (max-width: $viewport--xl - 1) {
    @content;
  }
}

@mixin viewport--md {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

@mixin viewport--sm {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

@mixin footer-line ($top) {
  &::before {
    position: absolute;
    display: block;
    content: '';
    top: $top;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $color-finlandia;
    opacity: 0.2;
  }
}

